:host {
  display: block;
  width: 100%;
  max-width: 600px;
  margin: auto;
  padding: 0 1rem;
  --p-iconfield-icon-color: var(--color-primary-color, #378bc0);

  @media (min-width: 640px) {
    padding: 0;
  }
}

// Screen reader only utility class
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

// Card grid layout for accounts
.account-cards-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 0.75rem;
  padding: 0.25rem;

  // 2-column layout for medium screens and above
  @media (min-width: 640px) {
    grid-template-columns: repeat(2, 1fr);
  }
}

// Individual account card styling
.account-card {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.875rem 1rem;
  background-color: var(--surface-card);
  border: 1px solid var(--surface-border);
  border-radius: var(--border-radius, 0.375rem);
  cursor: pointer;
  transition: all 0.2s ease;

  // Hover effects
  &:hover {
    background-color: var(--surface-hover);
    border-color: var(--primary-color, #378bc0);
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  }

  // Focus state for keyboard navigation (browser native focus)
  &:focus {
    outline: 2px solid var(--primary-color, #378bc0);
    outline-offset: 2px;
  }

  &:focus-visible {
    outline: 2px solid var(--primary-color, #378bc0);
    outline-offset: 2px;
  }

  // Programmatic focus state (via keyboard navigation)
  &.focused {
    outline: 2px solid var(--primary-color, #378bc0);
    outline-offset: 2px;
    background-color: var(--surface-hover);
    border-color: var(--primary-color, #378bc0);
    transform: translateY(-1px);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  }

  // Active/pressed state
  &:active {
    transform: translateY(0);
    box-shadow: none;
  }
}

// Avatar styling
.account-card-avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  min-width: 2.5rem;
  background-color: var(--color-primary-color, #378bc0);
  color: white;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 50%;
  text-transform: uppercase;
}

// Card content area
.account-card-content {
  flex: 1;
  min-width: 0; // Allows text truncation to work
  display: flex;
  flex-direction: column;
  gap: 0.125rem;
}

// Account name
.account-card-name {
  font-size: 0.9375rem;
  font-weight: 500;
  color: var(--text-color);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// Account owner/subtitle
.account-card-owner {
  font-size: 0.8125rem;
  color: var(--text-color-secondary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// Action indicator (chevron)
.account-card-action {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-color-secondary);
  opacity: 0;
  transition:
    opacity 0.2s ease,
    transform 0.2s ease;

  .account-card:hover &,
  .account-card:focus &,
  .account-card.focused & {
    opacity: 1;
    transform: translateX(2px);
  }

  i {
    font-size: 0.875rem;
  }
}

// Empty state styling
.account-cards-empty {
  min-height: 200px;
}
